import React, { useState,useEffect } from 'react';
import {useParams} from "react-router-dom";
import Typography from '@material-ui/core/Typography';
import { makeStyles } from '@material-ui/core/styles';
import { Grid, Paper, Box, Button, Divider, MenuItem, Select } from '@material-ui/core';
import { createMuiTheme } from '@material-ui/core/styles';
import SidebarVendor from '../../../components/SidebarVendor';
import CssBaseline from '@material-ui/core/CssBaseline';
import Lightbox from 'react-image-lightbox';
import 'react-image-lightbox/style.css'; 
import FormControl from '@material-ui/core/FormControl';
import api from '../../../services/api';

import { useHistory } from 'react-router-dom';


const hostIP = require('../../../services/hostIP.json');

const useStyles = makeStyles((theme) => ({
    root: {
        display: 'flex',
      },
    paper: {
        padding: theme.spacing(2),
        textAlign: 'center',
        border: '1px solid #c6c6c6 rgba(0,0,0,0.1)',
        color: theme.palette.text.secondary,
        '&:hover': {
            boxShadow: '1px 2px 3px 1px rgba(0,0,0,0.4)',
        }
    },
    customBox: {
        padding: theme.spacing(2),
        textAlign: 'center',
        '&:hover': {
            boxShadow: '1px 2px 3px 1px rgba(0,0,0,0.4)',
            border: '1px solid #c6c6c6 rgba(0,0,0,0.1)',
            cursor: 'pointer',
            padding: 20,
        }
    },
    // necessary for content to be below app bar
    toolbar: theme.mixins.toolbar,
    content: {
        flexGrow: 1,
        padding: theme.spacing(3),
    },
    categoryImg: {
        width: '80%',
        '@media (min-width:600px)': {
            width: '50%',
        },
        [theme.breakpoints.up('md')]: {
            width: '50%',
        }
    },
}));

function Products(props) {
    let { id } = useParams();
    //console.log(id);
    const history = useHistory();

    const [photos,setPhotos] = useState({
        photoIndex: 0,
        isOpen: false,
    });

    const [data, setData] = useState({
        product: null,
        vendor: null,
    });

    const [notFound, setNotFound] = useState(false);
    const [msg, setMsg] = useState("Buscando dados do produto...");
    const [images,setImages] = useState([]);

    const [tipo, setTipo] = useState(null);

    useEffect(() => {
        //console.log("Chamou Vendedores Locais: ");
        api.get('productData', {
            headers: {
                productid: id,
            }
        }).then(response => {
            //console.log("recebeu:", response.data);
            //console.log("recebeu2:", response.data.product[0]);
            //console.log("recebeu3:", response.data.vendor[0]);
            if (!response.data) {
                //console.log("entrou aqui");
                setNotFound(true);
                //console.log('entoru');
                setMsg("No foram encontrados vendedores dessa categoria em sua regio.");
            }
            setData( { product: response.data.product[0], vendor: response.data.vendor[0] } );
            setImages([hostIP.hostIP+response.data.product[0].pictures[0], hostIP.hostIP+response.data.product[0].pictures[1]]);
            setTipo(response.data.product[0].stock[0].tipo);
        }).catch(err => {
            //console.log(err);
            setNotFound(true);
            setMsg("No foi possvel realizar a comunicao com o servidor. Tente novamente!");
        })
    }, [id]);

    const classes = useStyles();

    const theme = createMuiTheme();

    theme.typography.h3 = {
        fontSize: '1.2rem',
        '@media (min-width:600px)': {
            fontSize: '1.5rem',
        },
        [theme.breakpoints.up('md')]: {
            fontSize: '2.4rem',
        },
    };
    theme.typography.h4 = {
        fontSize: '1.4rem',
        '@media (min-width:600px)': {
            fontSize: '1.4rem',
        },
        [theme.breakpoints.up('md')]: {
            fontSize: '1.5rem',
        }
    };

    return (
        <div className={classes.root}>
        <CssBaseline />
        <SidebarVendor currentPage={0} title={data.product != null ? data.product.name : "Produto"} />
        <main className={classes.content}>
        <div className={classes.toolbar} />
        { !notFound && data.product != null
        ? (
            <Grid container spacing={3}>
            <Grid item xs={12} sm={6}>
                    <Paper className={classes.customBox} onClick={() => setPhotos({...photos, isOpen: true })}>
                        <img alt="Imagem do Produto" src={images[photos.photoIndex]} className={classes.categoryImg} />
                    </Paper>
            </Grid>
            <Grid item xs={12} sm={6}>
                <Paper className={classes.paper}>
                    <Grid container direction="column">
                        <Grid item style={{marginBottom: 10, textAlign: 'left'}}>
                            <Typography variant="h6" color="textPrimary">Caractersticas</Typography>
                        </Grid>
                       
                        <Divider style={{marginBottom: 10}} />

                        <Grid item style={{marginBottom: 10}}>
                            <Typography variant="h6" color="textPrimary" align="left"><b>{data.product.name}</b></Typography>
                        </Grid>

                        <Grid item style={{marginBottom: 10}}>
                            <Typography variant="body1" color="textPrimary" align="left"><b>Descrio: </b>{data.product.desc}</Typography>
                        </Grid>

                        <Divider style={{marginBottom: 10}} />

                        <Grid item style={{marginBottom: 10}}>
                            <Typography variant="body1" color="textPrimary" align="left"><b>Vendedor: </b>{data.vendor.name}</Typography>
                        </Grid>

                        <Grid item style={{marginBottom: 10}}>
                            <Typography variant="body1" color="textPrimary" align="left"><b>Endereo: </b>{data.vendor.endereco[1].logradouro},
                            {data.vendor.endereco[5].numero} - {data.vendor.endereco[2].bairro}.</Typography>
                        </Grid>

                        {data.product.stock.length == 1
                        ?   <Grid item style={{marginBottom: 10}}>
                                <Typography variant="body1" color="textPrimary" align="left"><b>Estoque Disponvel: </b>{data.product.stock[0].quantidade}</Typography>
                             </Grid>
                        : null }

                        <Divider style={{marginBottom: 10}} />
                        <Grid container direction="row" alignItems="flex-end" justify="space-evenly" spacing={2}>
                            <Grid item xs={12} sm={6}>
                                <Typography variant="h5" color="textPrimary">R$ {data.product.price}</Typography>
                            </Grid>
                            <Grid item xs={6} sm={6}>
                            { data.product.stock.length > 1 
                                ? 
                                    <FormControl className={classes.formControl}>
                                    <Select
                                      defaultValue={""}
                                      displayEmpty
                                      className={classes.selectEmpty}
                                      inputProps={{ 'aria-label': 'Without label' }}
                                      onChange={(e) => setTipo(e.target.value)} >
                                    <MenuItem value="" disabled>Tipo do Produto</MenuItem>
                                    {data.product.stock.map((item) => (
                                      <MenuItem key={item.tipo} value={item.tipo}>{item.tipo} ( {item.quantidade} disponvel )</MenuItem>
                                    ))}

                                    </Select>
                                  </FormControl>
                                
                                : <Typography variant="body1">Produto com Tamanho nico</Typography> }

                            </Grid>
                        </Grid>
                    </Grid>
                </Paper>
            </Grid>
            </Grid>
        )
        : <Typography variant="h6"  align="center">{msg}</Typography>
        }

    

    {photos.isOpen && (
        <Lightbox
        mainSrc={images[photos.photoIndex]}
        nextSrc={images[(photos.photoIndex + 1) % images.length]}
        prevSrc={images[(photos.photoIndex + images.length - 1) % images.length]}
        onCloseRequest={() => setPhotos({...photos, isOpen: false })}
        onMovePrevRequest={() => setPhotos({...photos, photoIndex: (photos.photoIndex + images.length - 1) % images.length }) }
        onMoveNextRequest={() => setPhotos({...photos, photoIndex: ((photos.photoIndex + 1) % images.length) }) }
        />
    )}   

    </main>
    </div>
    );
}

export default Products;